@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body{font-family:'Roboto Mono', monospace}body::-webkit-scrollbar{width:5px}body::-webkit-scrollbar-track{background-color:pink}body::-webkit-scrollbar-thumb{background-color:#fff}#root{position:absolute;top:0;left:0;width:100%;min-height:100vh;display:grid;grid-template-columns:1fr 1280px 1fr}#root #header{position:fixed;top:0;left:0;width:100%;height:75px;background-color:transparent;z-index:1}#root #header.active{background-color:pink}.section{display:grid;grid-template-columns:1fr 1280px 1fr;grid-column:1 / 4}.section .background{grid-column:1 / 4;grid-row:1 / 1;width:100%;height:100%}.section.pink{color:#FFF;-webkit-clip-path:polygon(0 0, 100% 5%, 100% 95%, 0 100%);clip-path:polygon(0 0, 100% 5%, 100% 95%, 0 100%)}.section.pink:nth-child(2){-webkit-clip-path:polygon(0 0, 100% 0%, 100% 95%, 0 100%);clip-path:polygon(0 0, 100% 0%, 100% 95%, 0 100%)}.section.pink:nth-last-child(2){-webkit-clip-path:polygon(0 0, 100% 5%, 100% 100%, 0 100%);clip-path:polygon(0 0, 100% 5%, 100% 100%, 0 100%)}.section.pink .background{background-color:pink}.section.white{color:#000}.section.white .background{background-color:#fff}.section .inner{display:grid;grid-template-columns:repeat(3, 1fr);grid-column:2 / 3;grid-row:1 / 1;height:100%;padding:200px 100px;box-sizing:border-box}.section .inner .inner-wrapper{grid-column:1 / 4;grid-row:1 / 2;display:grid;justify-content:center;align-items:center;height:100%;grid-template-columns:1fr}.section .inner .inner-wrapper.main-second{grid-template-columns:2fr 1fr}.section .inner .inner-wrapper.second-main{grid-template-columns:1fr 2fr}.section .inner .inner-wrapper.second-main .main,.section .inner .inner-wrapper.second-main .second{display:flex;justify-content:center;align-items:center}.section-title{font-size:400%;letter-spacing:5px;font-weight:bold;margin-bottom:20px}.section-text{font-size:100%;letter-spacing:1px;line-height:2.5}#hero{min-height:106vh}#hero .inner{display:flex;justify-content:center;align-items:center;grid-column:1/4}#footer{background-color:#ddd;grid-template-rows:-webkit-max-content;grid-template-rows:max-content}#footer .inner{padding:50px;width:100%;grid-column:1/4}#footer .inner .section-text{font-size:100%;letter-spacing:1px;line-height:2.5;text-align:center;width:100%;grid-column:1/4}#footer .inner .section-text a{color:#000}@media (max-width: 1280px){#root,.section{grid-template-columns:1fr}#root #hero.inner,.section #hero.inner{grid-column:1;display:flex;justify-content:center;align-items:center}}

