@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap');
@import "./_Variables.sass";


body {
    font-family: 'Roboto Mono', monospace; }

body::-webkit-scrollbar {
  width: 5px; }


body::-webkit-scrollbar-track {
    background-color: $ff_pink; }


body::-webkit-scrollbar-thumb {
  background-color: $ff_white; }


#root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1280px 1fr;

    #header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 75px;
        background-color: transparent;
        z-index: 1;
        &.active {
            background-color: $ff_pink; } } }

.section {
    display: grid;
    grid-template-columns: 1fr 1280px 1fr;
    grid-column: 1 / 4;

    .background {
        grid-column: 1 / 4;
        grid-row: 1 / 1;
        width: 100%;
        height: 100%; }

    &.pink {
        color: #FFF;
        clip-path: polygon(0 0, 100% 5%, 100% 95%, 0 100%);
        &:nth-child(2) {
            clip-path: polygon(0 0, 100% 0%, 100% 95%, 0 100%); }
        &:nth-last-child(2) {
            clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 100%); }

        .background {
            background-color: $ff_pink; } }

    &.white {
        color: #000;
        .background {
            background-color: $ff_white; } }

    .inner {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column: 2 / 3;
        grid-row: 1 / 1;
        height: 100%;
        padding: 200px 100px;
        box-sizing: border-box;

        .inner-wrapper {
            grid-column: 1 / 4;
            grid-row: 1 / 2;
            display: grid;
            justify-content: center;
            align-items: center;
            height: 100%;
            grid-template-columns: 1fr;

            &.main-second {
                grid-template-columns: 2fr 1fr; }
            &.second-main {
                grid-template-columns: 1fr 2fr;

                .main, .second {
                    display: flex;
                    justify-content: center;
                    align-items: center; } } } } }


.section-title {
    font-size: 400%;
    letter-spacing: 5px;
    font-weight: bold;
    margin-bottom: 20px; }

.section-text {
    font-size: 100%;
    letter-spacing: 1px;
    line-height: 2.5; }



#hero {
    min-height: 106vh;
    .inner {
        display: flex;
        justify-content: center;
        align-items: center;
        grid-column: 1/4; } }

#about {}



#services {}



#testimonials {}



#contact {}



#footer {
    background-color: #ddd;
    grid-template-rows: max-content;
    .inner {
        padding: 50px;
        width: 100%;
        grid-column: 1/4;
        .section-text {
            font-size: 100%;
            letter-spacing: 1px;
            line-height: 2.5;
            text-align: center;
            width: 100%;
            grid-column: 1/4;
            a {
                color: $ff_black; } } } }

$breakpoint-1280: 1280px;

@media (max-width: $breakpoint-1280) {

    #root, .section {
        grid-template-columns: 1fr;
        #hero {
            &.inner {
                grid-column: 1;
                display: flex;
                justify-content: center;
                align-items: center; } } } }
