@import "./_Variables.sass";

#header {
    &.transparent {
        background-color: transparent; }

    .logo-wrapper {
        width: 150px;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        height: 100%;
        border: 5px solid transparent;
        box-sizing: border-box;
        margin-left: 50px;
        .logo-image {
            svg {
                width: 100%;
                textPath {
                    fill: #FFF; } } } }
    .nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 100%;
        margin-right: 30px;
        .link {
            color: $ff-white;
            text-decoration: none;
            padding: 0 20px; } } }
